*,
*::before,
*::after {
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}

body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  font-size: 16px;
  margin: 0;
}

details > summary {
  list-style: none;
}

details > summary::-webkit-details-marker {
  display: none;
}

a {
  text-decoration: none;
  color: inherit;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

article > * + * {
  margin-top: 1em;
}

input,
button,
textarea,
select {
  font: inherit;
}

.select:focus {
  outline: none;
}

select::-ms-expand {
  display: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.header {
  background-color: #e0d7d7;
  margin-top: 0;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
.header__content {
  min-height: 100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__input {
  border: 1px solid #c7c3c3;
  box-shadow: 0 2px 6px 1px rgba(34, 60, 80, 0.2) inset;
  width: 270px;
  height: 30px;
}

input::placeholder {
  color: #aba9a9;
}

.btn {
  background-color: #1169cf;
  color: #ffffff;
  border: none;
  padding: 8px 12px;
  border-radius: 5px;
  text-shadow: 3px 4px 5px rgba(34, 60, 80, 0.2);
  box-shadow: 0 5px 10px 2px rgba(34, 60, 80, 0.2);
}

.btn:not(:last-child) {
  margin-right: 15px;
}

.calendar {
  padding-top: 20px;
}
.calendar__heading {
  display: flex;
}
.calendar__btn {
  display: inline;
  background-color: #ffffff;
  border: 1px solid #c7c3c3;
  border-radius: 5px;
  width: 20px;
  height: 20px;
}
.calendar__month {
  font-weight: 400;
  margin: 0;
  padding-left: 7px;
  padding-right: 7px;
}
.calendar__today {
  font-size: 0.8em;
  border: 1px solid #c7c3c3;
  border-radius: 3px;
  margin: 0;
  margin-left: 12px;
  padding: 3px 5px;
}

.left-arrow,
.right-arrow {
  border: 5px solid transparent;
  display: inline-flex;
  align-self: center;
}

.left-arrow {
  border-right: 5px solid rgb(128, 128, 128);
  border-left: none;
}

.right-arrow {
  border-left: 5px solid rgb(128, 128, 128);
  border-right: none;
}

.table {
  text-indent: initial;
  white-space: normal;
  text-align: start;
  border-spacing: 2px;
  border-color: rgb(128, 128, 128);
  font-variant: normal;
  border: 1px solid rgb(128, 128, 128);
}

td,
th {
  border: 0.16px solid rgba(200, 200, 200, 0.6);
}

.container {
  margin: 0 auto;
  max-width: 1240px;
}